* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  max-width: 100%;
  display: block;
}

button {
  cursor: pointer;
  background-color: #f4b700;
  border: none;
  border-radius: 10px;
  padding: 10px 40px;
  font-size: 18px;
}

button:focus, button:hover {
  background-color: #f37f04;
  outline: none;
  box-shadow: 4px 4px 4px 2px rgba(68, 68, 75, .2);
}

input {
  margin-right: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 20px;
}

span {
  font-size: 25px;
  font-weight: bold;
}

.timer {
  display: flex;
}

.field {
  padding-right: 10px;
}

